import React, { useEffect, useRef, useState } from "react";
import {
  CContainer,
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CTabs,
  CNav,
  CListGroup,
  CListGroupItem,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";

import "../../tarjetas/estadisticas/Estadisticas.css";
import {
  getRecursosAPI,
  getElementoById,
  getTarjetaEstadistica,
} from "src/helpers/peticionesAPI";
import ElementosEstadisticaSensores from "./ElementosEstadisticaSensores";
import ElementosEstadisticaSensor from "./ElementosEstadisticaSensor";
import Loader from "src/reusable/Loader";
import Navegacion, { NavegacionTipo } from "../../general/Navegacion";
import iconoactivo from '../../../assets/icons/activo.svg';
import iconoinactivo from '../../../assets/icons/inactivo.svg';
import './Estadisticas.css'

const ElementosEstadisticas = ({
  match: {
    params: { idElemento },
  },
  history
}) => {
  const [sensoresElementosEstad, setSensoresElementosEstad] = useState([]);
  const [graficasElementosEstad, setGraficasElementosEstad] = useState([]);
  const [informacion, setInformacion] = useState({
    nombre: "",
    identificador: "",
    descripcion: "",
    area: "",
    linea: "",
    tipo: "",
    proceso: "",
    estado:"inactivo",
    icono:iconoinactivo
  });
  const [cargando, setCargando] = useState(true);
  const intervalo = useRef(null);

  const token = localStorage.getItem("token");
  const id = idElemento;

  const getValor = async () => {
    const {
      nombre,
      identificador,
      descripcion,
      area,
      linea,
      proceso,
      tipo,
      estado,
      sensores,
      graficas,
    } = await getElementoById(token, id);
    let tarjeta = "";
    if(sensores.length > 0)
      tarjeta = sensores[0].direccion.substr(0,5);  
    const { sensores: lista_sensores } = await getRecursosAPI(tarjeta, token);

    let str_sensores = [];
    sensores.forEach((sensor) => {
      str_sensores.push(`${sensor.direccion}`);
    });
    const { sensores: sensores_estadistica } = await getTarjetaEstadistica(
      str_sensores,
      token
    );

    let arr_sensores_variables = [];
    sensores.map((sensor, idx) => {
      const direccion_sensor = sensor.direccion;
      const sensor_direccion = direccion_sensor.slice(
        direccion_sensor.indexOf(".") + 2,
        direccion_sensor.length
      );
      const sensor_info = lista_sensores.find(
        (s) => s.index === parseInt(sensor_direccion)
      );
      arr_sensores_variables.push({
        nombre: sensor_info.nombre,
        alias: sensor_info.alias,
        estado: sensor_info.estado,
        unidades: sensor_info.unidades,
        clase: "S",
        ...sensores_estadistica[0][idx],
      });
    });
    setSensoresElementosEstad(arr_sensores_variables);

    let arr_graficas = [];
    graficas.forEach((grafica) => {
      let arr_grafica = [];
      grafica.direcciones.forEach((direccion) => {
        if (direccion[0] === "S") {
          const direccion_elemento = parseInt(direccion.substring(1));
          const direccion_sensor = sensores[direccion_elemento].direccion;
          const sensor_index = direccion_sensor.slice(
            direccion_sensor.indexOf(".") + 2,
            direccion_sensor.length
          );
          const sensor_info = lista_sensores.find(
            (sensor) => sensor.index === parseInt(sensor_index)
          );
          if (sensor_info.estado === "activo") {
            arr_grafica.push({
              nombre: sensor_info.nombre,
              alias: sensor_info.alias,
              estado: sensor_info.estado,
              unidades: sensor_info.unidades,
              clase: "S",
              ...sensores_estadistica[0][direccion_elemento],
            });
          }
        }
      });
      if (arr_grafica.length > 0) {
        arr_graficas.push({
          nombre: grafica.nombre,
          datos: { tipo: "sensores", valores: arr_grafica },
        });
      }
    });
    setGraficasElementosEstad(arr_graficas);

    const icono = (estado=="activo")?iconoactivo:iconoinactivo;      
    const estadoformato = estado[0].toUpperCase() + estado.substring(1);
    setInformacion({
      nombre,
      identificador,
      descripcion,
      area,
      linea,
      tipo,
      proceso,
      estado: estadoformato,
      icono,
    });
  };
  const getIntervalo = ()=>{
    return setInterval(
      getValor,
      30000
    );
  }

  useEffect(()=>{
    return ()=>{
      if(intervalo.current!=null)
        clearInterval(intervalo.current);
    }
  },[]);
  useEffect(async () => {
    setCargando(true);
    if(intervalo.current!=null){
      clearInterval(intervalo.current);
      intervalo.current = null;
    }
    await getValor();
    intervalo.current = getIntervalo();
    setCargando(false);
  }, [idElemento]);
  
  if(cargando)
    return  (<div style={{display:'grid',placeItems:'center',height:'40vh'}}>
                <Loader height="10vh"/>
            </div>);
  return (
    <CContainer fluid>
      <Navegacion activo={3} tipo={NavegacionTipo.ELEMENTO} history={history} direccion={id}/>
      <CRow>
        <CCol sm="12">        
          <CCard className="shadow">
            <CCardBody>
              <CRow className="justify-content-between">
                <CCol className="col-3 d-flex align-items-center">
                  <img src={informacion.icono} className="icono"/>
                  <h4 className="ml-2">{informacion.estado}</h4>
                </CCol>
                <CCol className="col-9 text-right">
                  <h5>Elemento</h5>
                  <h3>{`${informacion.nombre}`}</h3>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CRow>
        <CCol>
          <CCard className="shadow">
            <CCardHeader className="contenedor-cabecera">
              <h4>General</h4>
            </CCardHeader>
            <CCardBody>
              <CListGroup accent>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Identificador :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.identificador}
                  </CCol>
                </CRow>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Descripci贸n :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.descripcion}
                  </CCol>
                </CRow>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Area :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.area}
                  </CCol>
                </CRow>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Linea :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.linea}
                  </CCol>
                </CRow>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Proceso :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.proceso}
                  </CCol>
                </CRow>
                <CRow>
                  <CCol sm="4">
                    <CListGroupItem accent="info">Tipo :</CListGroupItem>
                  </CCol>
                  <CCol sm="4" className="align-self-center">
                    {informacion.tipo}
                  </CCol>
                </CRow>
              </CListGroup>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CRow>
        <CCol sm="12">
          <CCard className="shadow">
            <CCardHeader className="contenedor-cabecera">
              <h4>Sensores</h4>
            </CCardHeader>
            <CCardBody>
              <CTabs activeTab="grupos">
                <CNav variant="tabs">
                  <CNavItem>
                    <CNavLink data-tab="grupos">Grupos</CNavLink>
                  </CNavItem>
                  {/* <CNavItem>
                    <CNavLink data-tab="todos">Todos</CNavLink>
                  </CNavItem> */}
                </CNav>
                <CTabContent>
                  <CTabPane data-tab="grupos">
                    {graficasElementosEstad.map((grafica, idx) => {
                      return (
                        <div className="contenedor-cabecera-grafica"  key={idx}>
                          <h4 className="my-4">{grafica.nombre}</h4>
                          <ElementosEstadisticaSensores
                            sensores={grafica.datos}
                          />
                        </div>
                      );
                    })}
                  </CTabPane>

                  <CTabPane data-tab="todos">
                    <CRow>
                      {sensoresElementosEstad.map((sensor, idx) => {
                        return (
                          <ElementosEstadisticaSensor
                            sensor={sensor}
                            key={idx}
                          />
                        );
                      })}
                    </CRow>
                  </CTabPane>
                </CTabContent>
              </CTabs>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default ElementosEstadisticas;
